'use client';

import * as React from 'react';
import * as SeparatorPrimitive from '@radix-ui/react-separator';

import { cn } from '@/lib/utils'; // Pastikan path ke file utils Anda benar

const Separator = React.forwardRef<
  React.ElementRef<typeof SeparatorPrimitive.Root>,
  React.ComponentPropsWithoutRef<typeof SeparatorPrimitive.Root>
>(({ className, orientation = 'horizontal', decorative = true, ...props }, ref) => (
  <SeparatorPrimitive.Root
    ref={ref}
    decorative={decorative}
    orientation={orientation}
    className={cn(
      'shrink-0 bg-border', // Style dasar: tidak menyusut dan warna latar dari variabel CSS 'border'
      orientation === 'horizontal' ? 'h-[1px] w-full' : 'h-full w-[1px]', // Style kondisional: horizontal atau vertikal
      className // Menggabungkan class tambahan dari props
    )}
    {...props}
  />
));
Separator.displayName = SeparatorPrimitive.Root.displayName;

export { Separator };